:root {
  --items: 4;
  --indent: 24px;
}

.search-form {
  background-color: rgba(176, 48, 214, .5);
  justify-content: center;
  gap: 10px;
  padding: 10px 0;
  display: flex;
}

.search-input {
  min-width: 265px;
}

.gallery {
  gap: var(--indent);
  flex-wrap: wrap;
  flex-basis: calc((100% - var(--indent) * (var(--items)  - 1) ) / var(--items) );
  display: flex;
}

.photo-card {
  width: 270px;
  border-radius: 0 0 4px 4px;
  flex-wrap: wrap;
  display: flex;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
}

.photo-card img {
  height: 100%;
  width: 100%;
  object-fit: cover;
  display: block;
}

.info {
  justify-content: space-between;
  display: flex;
}

.info-item {
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  font-size: 13px;
  display: flex;
}

button {
  cursor: pointer;
  font-weight: Medium;
  justify-content: center;
  align-items: center;
  padding: 5px;
  display: flex;
}

button:hover, button:focus {
  background-color: #c1d3d3;
}

.load-more {
  margin: 10px auto 0;
}

